<template>
  <div>
    <div class="scrollNav">
      <v-row class="nav_con">
        <v-col class="nav_logo" cols="4">
          <router-link to="/">
            <img src="../../assets/logo/scrollblogo.svg" class="blogo"
          /></router-link>
        </v-col>
        <v-spacer />
        <v-col cols="8" class="menu_con">
          <div class="menu_list">
            <template v-for="(item, i) in menus" :key="item.value">
              <router-link :to="item.value">
                <v-list-item :value="i">
                  <v-list-item-title>{{ item.title }}</v-list-item-title>
                </v-list-item>
              </router-link>
            </template>
          </div>
        </v-col>
        <v-col cols="8" class="small_nav">
          <div class="ham" ref="hamElement" @click="stopScrolling">
            <div class="bar_con">
              <span class="bar1"></span>
              <span class="bar2"></span>
              <span class="bar3"></span>
            </div>
          </div>
          <!-- <div class="nav_btn">
            <v-icon
              v-if="!menu_state"
              @click="menu_state = true"
              class="btn_close"
              >mdi-menu</v-icon
            >
            <v-icon
              v-if="menu_state"
              @click="menu_state = false"
              class="btn_open"
              >mdi-menu-open</v-icon
            >
          </div> -->
        </v-col>
      </v-row>
    </div>
    <Menu v-if="menu_state" />
  </div>
</template>

<script>
import Menu from "./../header/Menu.vue";

export default {
  name: "ScrollNav",
  components: {
    Menu,
  },
  props: ["nav_state"],
  computed: {
    navState() {
      return this.nav_state;
    },
  },
  data() {
    return {
      nav_State: null,
      menu_state: false,
      scrollLock: false,
      goto_subpage: false,
      menus: [
        {
          title: "회사소개",
          value: "/Intro",
        },
        {
          title: "회사소식",
          value: "/News",
        },
        {
          title: "서비스",
          value: "/Service",
        },
        {
          title: "문의&의뢰",
          value: "/Contact",
        },
        {
          title: "채용",
          value: "/Employ",
        },
      ],
    };
  },
  watch: {
    navState: {
      immediate: true,
      handler(newValue) {
        this.nav_State = { ...newValue }; // 객체를 복사하는 경우
        if (this.navState) {
          this.headerMenu();
        }
      },
    },
  },

  mounted() {
    this.$nextTick(() => {
      const hamElement = this.$refs.hamElement;

      hamElement.addEventListener("click", function () {
        this.classList.toggle("active");
      });
    });
  },
  beforeUnmount() {
    this.removeStopEvent();
  },
  methods: {
    headerMenu() {
      const ham = document.querySelectorAll(".ham");
      ham.forEach((el) => {
        el.classList.toggle("active");
      });
      this.stopScrolling();
    },
    stopScrolling() {
      console.log(this.navState);
      this.scrollLock = !this.scrollLock;
      this.menu_state = !this.menu_state;
      this.$emit("update-scroll_state", this.menu_state);

      if (this.scrollLock) {
        window.addEventListener("wheel", this.stopEvent, {
          passive: false,
        });
        window.addEventListener("touchmove", this.stopEvent, {
          passive: false,
        });
      } else {
        window.removeEventListener("wheel", this.stopEvent);
        window.removeEventListener("touchmove", this.stopEvent);
      }
    },
    stopEvent(e) {
      e.preventDefault();
      e.stopPropagation();
    },

    removeStopEvent() {
      if (this.scrollLock) {
        window.removeEventListener("wheel", this.stopEvent, { passive: true });
        window.removeEventListener("touchmove", this.stopEvent, {
          passive: true,
        });
      }
    },

    // stopScrolling() {
    //   this.scrollLock = !this.scrollLock;
    //   this.menu_state = !this.menu_state;
    // },
  },
};
</script>

<style lang="scss" scoped>
.scrollNav {
  z-index: 1000;
  position: fixed;
  transition: all 1s ease;
  width: 100vw;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 1px 0px 0px rgb(219, 211, 211);
}

.nav_con {
  position: relative;
  height: 100%;
  margin: auto;

  .nav_logo {
    cursor: pointer;
    display: flex;
    align-items: center;
    a {
      display: flex;
      align-items: center;
    }
  }
}

.menu_con {
  align-self: center;

  .menu_list {
    position: relative;
    display: flex;
    justify-content: flex-end;
    background: none;

    .v-list-item-title {
      font-family: "Pretendard-Regular";
      font-size: 1.125rem;
      font-weight: bold;
      color: rgb(0, 0, 0);
    }
  }

  .v-list-item-title:hover {
    color: #0174f5;
  }

  .menu_list a {
    text-decoration: none;
  }
}
.small_nav {
  display: none;
  justify-content: right;
  align-items: center;
  width: 100%;
  height: 100%;

  .ham {
    width: 60px;
    height: 55px;
    display: flex;
    justify-content: right;
    align-items: center;
    cursor: pointer;
    span {
      display: block;
      background: #000000;
      border-radius: 3px;
      transition: all 0.125s;
    }
    .bar1 {
      margin-bottom: 4px;
    }
    .bar2 {
      opacity: 1;
    }
    .bar3 {
      margin-top: 4px;
    }
  }
}
.ham.active {
  span {
    transition: all 0.125s;
  }
  .bar1 {
    margin-bottom: -2px;
    transform: rotate(-45deg);
  }
  .bar2 {
    opacity: 0;
  }
  .bar3 {
    margin-top: -2px;
    transform: rotate(45deg);
  }
}

@media screen and (min-width: 1280px) {
  .scrollNav {
    height: 74px;
    top: -75px;
  }
  .nav_con {
    width: 1280px;
  }
  .small_nav {
    display: none;
  }
  .menu_con {
    display: flex;
    justify-content: right;
    .menu_list {
      padding-right: 16px;
    }
  }
  .blogo {
    width: 160px;
    height: 50px;
  }
}
// 노트북
@media screen and (min-width: 760px) and (max-width: 1280px) {
  .scrollNav {
    height: 60px;
    top: -61px;
  }
  .nav_con {
    width: 800px;
  }
  .small_nav {
    display: none;
  }
  .menu_con {
    display: flex;
    justify-content: right;
    .menu_list {
      padding-right: 16px;
    }
  }
  .blogo {
    width: 140px;
    height: 40px;
  }
}
// 태블릿
@media screen and (min-width: 640px) and (max-width: 759px) {
  .scrollNav {
    height: 60px;
    top: -60px;
  }
  .menu_con {
    display: none;
  }
  .small_nav {
    display: flex;
  }
  .nav_con {
    width: 100vw;
  }
  .blogo {
    width: 120px;
    height: 30px;
  }
  .ham {
    span {
      width: 30px;
      height: 3px;
    }
  }
  .ham.active {
    .bar1 {
      margin-bottom: -4px;
    }
  }
}
// 핸드폰
@media screen and (min-width: 320px) and (max-width: 639px) {
  .scrollNav {
    height: 55px;
    top: -56px;
  }
  .nav_con {
    width: 100vw;
  }
  .small_nav {
    display: flex;
  }
  .menu_con {
    display: none;
  }
  .blogo {
    width: 100px;
    height: 24px;
  }
  .nav_btn {
    font-size: 20px;
  }
  .ham {
    span {
      width: 20px;
      height: 2px;
    }
  }
  .ham.active {
    .bar1 {
      margin-bottom: -2px;
    }
  }
}
</style>
